.credit-card {
  height: 200px;
  width: 340px;
  // background-image: url("../../../../public/assets/img/credit-cards/credit-card-3.svg");
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 10px;
  padding: 20px;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transform: scale(1);
  transition: all 0.3s ease;
  cursor: pointer;
  .top {
    display: flex;
    align-items: normal;
    justify-content: space-between;
    .label {
      font-size: 10px;
    }
    .value {
      margin-bottom: 10px;
    }
    .type {
      img {
        height: 34px;
      }
    }
  }
  .bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .label {
      font-size: 10px;
    }
    .value {
      margin-bottom: 10px;
    }
    .expiry-date {
      color: #404a60;
    }
  }
}
.credit-card:hover {
  // transform: scale(1.02);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
}
.new-card {
  cursor: pointer;
  background: #20b7571b;
  border: 1.5px dashed #20b757;
  border-radius: 10px;
  height: 200px;
  width: 340px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  .icon {
    font-size: 24px;
    color: #20b757;
    margin-bottom: 10px;
  }
}
.new-card:hover {
  background: #20b7573b;
}
