.credit-cards-container {
  .header {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  .metrics {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  .actions {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;

    .title {
      font-size: 18px;
      font-weight: 600;
    }
  }
  .credit-card-list {
    margin-top: 40px;
    background: #fff;
    padding: 20px;
    border-radius: 12px;
    .cards {
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
    }
  }
}
